<template>
  <v-container fluid fill-height style="padding:0;" id="regexp" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex xs12 sm6 pr-1>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="a1">声明正则表达式</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <ul>
                        <li>const reg = /\w+/g;</li>
                        <li>const reg = new RegExp('\\w+', 'g');</li>
                        <li>const reg = RegExp('\\w+', 'g');</li>
                    </ul>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12 sm6>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="a2">正则表达式常用元字符</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                     <ul>
                        <li><code>\d</code>：数字0~9</li>
                        <li><code>\s</code>：空字符spaces,tabs,newlines</li>
                        <li><code>\w</code>：字母 a-ZA-Z_</li>
                        <li><code>\b</code>：简单点理解，是用来表示\w单词的边界 "1 11 111 22".match(/\b\d\d\b/g) -> ["11", "22"]</li>
                        <li><code>\D</code>：非数字字符</li>
                        <li><code>\S</code>：非空格字符</li>
                        <li><code>\W</code>：非字母字符</li>
                        <li><code>\B</code>：非边界</li>
                        <li><code>.</code>：匹配任意字符，除了newlines</li>
                    </ul>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12 sm6 pr-1>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="380"
              >
                <v-card-title>
                  <h3 id="a3">特殊字符</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <ul>
                        <li>需要转义的字符：<code>. ( ) \ / [ ]</code></li>
                        <li><code>[]</code>用法
                            <ul>
                                <li><code>[abc]</code>，<code>[a-z]</code>：表示多个字符中的一个</li>
                                <li><code>[^abc]</code>：表示除了abc以外的字符</li>
                            </ul>
                        </li>
                        <li>
                            量词
                            <ul>
                                <li><code>\d{5}</code>：表示5个数字</li>
                                <li><code>\d{3,5}</code>：表示 >=3 <=5 个字符</li>
                                <li><code>\d{3,}</code>：表示 >=3 个字符</li>
                                <li><code>+</code>：一个或一个以上</li>
                                <li><code>?</code>：0个或者一个</li>
                                <li><code>*</code>：0个或者多个</li>
                            </ul>
                        </li>
                        <li>
                            <code>a|b|c</code>：表示a或b或c
                        </li>
                        <li>
                            <code>/^abc$/</code>：精确匹配abc
                        </li>
                    </ul>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12 sm6>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="380"
              >
                <v-card-title>
                  <h3 id="a4">模式flag</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <ul>
                        <li><code>g</code>：全局匹配</li>
                        <li><code>i</code>：忽略大小写</li>
                        <li><code>m</code>：多行匹配</li>
                        <li><code>s</code>：允许<code>.</code>匹配换行符</li>
                        <li><code>u</code>：将字符串看成unicode字符</li>
                        <li><code>y</code>：正则表达式设置lastIndex会基于此索引开始匹配</li>
                    </ul>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12 sm6 pr-1>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="a5">跨行匹配</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <pre class="code">{{pres[0]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12 sm6>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="a6">y模式</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <pre class="code">{{pres[1]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex xs12 sm6 pr-1>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="380"
              >
                <v-card-title>
                  <h3 id="a7">分组</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. 在正则表达式中使用圆括号可以对匹配结果分组 </p>
                    <pre class="code">{{pres[2]}}</pre>
                    
                    <p class=".body-1">2. 可以取消分组 </p>
                    <pre class="code">{{pres[3]}}</pre>

                    <p class=".body-1">3. 可以在正中直接引用分组 </p>
                    <pre class="code">{{pres[4]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12 sm6>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="a8">正则表达式实例方法</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. <code>test</code>方法用来判断是否包含匹配字符串 </p>
                    <p class=".body-1">2. <code>test</code>如果开启了<code>g</code>标记，重复调用test方法，正则的<code>lastIndex</code>会表示最新匹配结果的开始索引</p>
                    <pre class="code">{{pres[5]}}</pre>

                    <p class=".body-1">3. <code>exec</code>方法用来提取匹配的字符串 </p>
                    <pre class="code">{{pres[6]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="a9">正则表达式字符串方法</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. <code>match</code>返回匹配的字符串数组 </p>
                    <pre class="code">{{pres[7]}}</pre>

                    <p class=".body-1">2. <code>matchAll</code>返回匹配的结果的可迭代对象 </p>
                    <pre class="code">{{pres[8]}}</pre>

                    <p class=".body-1">3. <code>replace</code>将匹配字符串替换成指定的，方法的第二个参数可以是字符串也可以是函数
                        <ul>
                            <li>字符串：需要替换的字符串，此字符串可以使用$反向引用
                                <table>
                                    <tr>
                                        <td> </td>
                                        <th>名称</th>
                                        <th colspan="2">示例</th>
                                        <th>输出</th>
                                        <th>说明</th>
                                    </tr>
                                    <tr>
                                        <th>1</th>
                                        <td>$$</td>
                                        <td rowspan="5"><pre>{{pres[9]}}</pre></td>
                                        <td>str.replace(reg, "$$");</td>
                                        <td>"$$aa"</td>
                                        <td>表示$字符</td>
                                    </tr>
                                    <tr>
                                        <th>2</th>
                                        <td>$&</td>
                                        <td>str.replace(reg, "$&|");</td>
                                        <td>"aa-|aa-|aa"</td>
                                        <td>表示匹配成功的字符串</td>
                                    </tr>
                                    <tr>
                                        <th>3</th>
                                        <td>$`</td>
                                        <td>str.replace(reg, "$`&lt;");</td>
                                        <td>"&lt;aa-&lt;aa"</td>
                                        <td>表示匹配到的字符之前的所有字符</td>
                                    </tr>
                                    <tr>
                                        <th>4</th>
                                        <td>$'</td>
                                        <td>str.replace(reg, "$'&gt;");</td>
                                        <td>"aa-aa&gt;aa&gt;aa"</td>
                                        <td>表示匹配到的字符之后的所有字符</td>
                                    </tr>
                                    <tr>
                                        <th>5</th>
                                        <td>$n</td>
                                        <td>str.replace(reg, "$1+");</td>
                                        <td>"aa+aa+aa"</td>
                                        <td>表示指定第几个分组括号中的字符</td>
                                    </tr>
                                </table>
                            </li>
                            <li>函数：返回值表示最终替换的字符串
                                <pre class="code">{{pres[10]}}</pre>
                            </li>
                            <li>
                                <code>reg[Symbol.replace](str, ...);</code>这种调用方式和上面等效
                            </li>
                        </ul>
                    </p>
                    
                    <p class=".body-1">4. <code>search</code>查找第一个匹配的索引 </p>
                    <pre class="code">{{pres[11]}}</pre>

                    <p class=".body-1">5. <code>split</code>将字符串转换成数组 </p>
                    <pre class="code">{{pres[12]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import { setInterval } from "timers";
export default {
  components: { Blog },
  data() {
    return {
      time: "2018-12-28",
      title: "正则表达式",
      items: [
        {
          title: "声明正则表达式",
          target: "#a1"
        },
        { divider: true },
        {
          title: "正则表达式常用元字符",
          target: "#a2"
        },
        { divider: true },
        {
          title: "特殊字符",
          target: "#a3"
        },

        {
          title: "模式flag",
          target: "#a4"
        },
        { divider: true },
        {
          title: "跨行匹配",
          target: "#a5"
        },
        { divider: true },
        {
          title: "y模式",
          target: "#a6"
        },
        {
          title: "分组",
          target: "#a7"
        },
        { divider: true },
        {
          title: "正则表达式实例方法",
          target: "#a8"
        },
        { divider: true },
        {
          title: "正则表达式字符串方法",
          target: "#a9"
        }
      ],
      pres: 
[
    `
    const reg = 'ab\\nc!';
    reg.match(/b[^]*c/m);

    输出
    ["b↵c", index: 1, input: "ab↵c!", groups: undefined]
    `,
    `
    const reg = /abc/y;
    reg.lastIndex = 2; // 开始匹配的索引
    reg.test('aaabc'); // true
    `,
    `
    let str = "aa-bb-aa";
    let res = str.match(/(a)+/);
    console.log(res); 

    输出
    [ "aa",      "a",     index: 0, input: "aa-bb-aa", groups: undefined]
        ^         ^
    正则匹配结果 圆括号匹配
    `,
    `
    let str = "aa-bb-aa";
    let res = str.match(/(?:a)+/);
    console.log(res); 

    输出
    ["aa", index: 0, input: "aa-bb-aa", groups: undefined]
    `,
    `
    let str = "aa-bb-aa";
    let res = str.match(/(a)\\1/);
    console.log(res); 

    输出
    ["aa", "a", index: 0, input: "aa-bb-aa", groups: undefined]
    `,
    `
    const str = "aa-aa-aa";
    const reg = /(a*)-/g;
    let res = reg.test(str);
    while(res) {
        console.log(reg.lastIndex);
        res = reg.test(str);
    }

    输出
    3
    6
    `,
    `
    const str = "aa-aa-aa";
    const reg = /a*-/g;
    let res = reg.exec(str);
    while(res !== null) {
        console.log(res);
        res = reg.exec(str);
    }

    输出
    ["aa-", index: 0, input: "aa-aa-aa", groups: undefined]
    ["aa-", index: 3, input: "aa-aa-aa", groups: undefined]
    `,
    `
    const str = "aa-aa-aa";
    const reg = /(a*)-/g;
    str.match(reg); 等价于 reg[Symbol.match](str);

    输出
    ["aa-", "aa-"]
    `,
    `
    const str = "aa-aa-aa";
    const reg = /(a*)-/;
    let res = str.matchAll(reg); 等价于 reg[Symbol.matchAll](str)
    console.log([...res]);

    输出
    [["aa-", "aa", index: 0, input: "aa-aa-aa", groups: undefined]]
    `,
`
const str = "aa-aa-aa";
const reg = /(a*)-/g;
`,
    `
    const str = "aa-aa-aa";
    const reg = /(a*)-/g;
    str.replace(reg, function(...params){
        // params数组表示匹配的结果
        // ["aa-", "aa", 0, "aa-aa-aa"]
        // 第一个参数，匹配的字符
        // 第二个参数，小括号的字符
        // 第三个参数，匹配的字符的索引位
        // 第四个参数，原始字符
        return 'A';
    });

    输出
    "AAaa"
    `,
    `
    const str = "aa-aa-aa";
    const reg = /(a*)-/g;
    str.search(reg); 等价于 reg[Symbol.search](str);

    输出 
    0
    `,
    `
    // split 
    const str = "aa-bb-aa";
    const reg = /-/g;
    str.split(reg); 等价于 reg[Symbol.split](str);

    输出
    ["aa", "bb", "aa"]
    `

]
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

<style lang="scss">
#regexp {
  .blogcontent {
    padding-top: 20px;
  }
  .code {
    font-size: 12px;
    background-color: #f9f9f5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }

  table {
        border-collapse: collapse;
        border: 2px solid rgb(100, 100, 100);
        letter-spacing: 1px;
        font-family: sans-serif;
        font-size: 0.7rem;
    }

    td,
    th {
        border: 1px solid rgb(100, 100, 100);
        padding: 10px 10px;
    }
}
</style>

